.b_top_div {
    display: flex;
}

.b_top_item {
    text-align: center;
    width: 100%;
    height: 5vh;
    line-height: 5vh;
    font-weight: bold;
    color: var(--main-text-color-0);
}

.b_bottom_div {
    display: flex;
}

.b_bottom_item {
    text-align: center;
    width: 100%;
    margin: 5px;
    height: 5vh;
    line-height: 5vh;
    font-weight: bold;
    color: var(--main-text-color-0);
    background: var(--main-bg-color-1);
}

.b_head {
    width: calc(100% - 10px);
    height: 10vh;
    margin: 5px;
    display: flex;
    justify-content: center;
}

.b_head_mid {
    width: 10vw;
    height: 100%;
    background: var(--main-bg-color-1);
    border-radius: 10px;
    cursor: pointer;
}

.b_body {
    width: calc(100% - 10px);
    height: 20vh;
    margin: 5px;
    display: flex;
    justify-content: center;
}

.b_body_left {
    width: 10vw;
    height: 100%;
    background: var(--main-bg-color-1);
    border-radius: 10px;
    cursor: pointer;
}

.b_body_mid {
    width: 20vw;
    height: 100%;
    background: var(--main-bg-color-1);
    border-radius: 10px;
    margin-left: 5vw;
    margin-right: 5vw;
    cursor: pointer;
}

.b_body_right {
    width: 10vw;
    height: 100%;
    background: var(--main-bg-color-1);
    border-radius: 10px;
    cursor: pointer;
}


.b_foot {
    width: calc(100% - 10px);
    height: 30vh;
    margin: 5px;
    display: flex;
    justify-content: center;
}

.b_foot_left {
    width: 10vw;
    height: 100%;
    background: var(--main-bg-color-1);
    border-radius: 10px;
    margin-right: 3vw;
    cursor: pointer;
}

.b_foot_right {
    width: 10vw;
    height: 100%;
    background: var(--main-bg-color-1);
    border-radius: 10px;
    margin-left: 3vw;
    cursor: pointer;
}