/*layout*/
.header {
    position: absolute;
    top: 0px;
    right: 0;
    left: 0;
    height: 50px;
    width: 100vw;
    line-height: 50px;
    display: -webkit-flex; /* Safari */
    display: flex;
}

.header .i-icon {
    box-shadow: var(--box-shadow) !important;
    border-radius: 20px !important;
    padding: 5px !important;
    text-align: center;
}

.header-nav-left {
    width: calc(33.3% - 5px);
    text-align: left;
    padding-left: 5px;
    color: var(--primary-color);
    cursor: pointer;
    font-size: 30px;
}

.header-nav-mid {
    width: 33.3%;
    text-align: center;
    font-weight: bold;
}


.header-nav-right {
    width: calc(33.3% - 5px);
    text-align: right;
    padding-right: 5px;
    color: var(--primary-color);
    cursor: pointer;
    font-size: 25px;
}

.main {
    position: absolute;
    top: 50px;
    bottom: 85px;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.top-main {
    position: absolute;
    top: 50px;
    bottom: 20px;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.bottom-main {
    position: absolute;
    top: 0px;
    bottom: 80px;
    right: 0;
    left: 0;
    width: 100vw;
    overflow: auto;
}

.footer {
    position: absolute;
    bottom: 0px;
    right: 0;
    left: 0;
    width: 100vw;
    height: 80px;
    line-height: 80px;
}

.footer-nav {
    display: -webkit-flex; /* Safari */
    display: flex;
}

.footer-menu {
    text-align: center;
    font-size: 25px;
    color: var(--primary-color);
    cursor: pointer;
}

.footer-nav-menu {
    width: 50%;
    text-align: center;
    color: gray;
    cursor: pointer;
    box-shadow: var(--box-shadow);
}

.footer-nav-menu.active {
    color: var(--primary-color);
    font-weight: bold;
    box-shadow: none;
}

/*表单*/
.sys-form-title {
    margin: 5px;
    padding: 5px;
    width: calc(100% - 20px);
    height: 50px;
    line-height: 50px;
    font-size: 30px;
}

.line-row {
    margin-bottom: 10px;
}

.sys-input {
    margin: 5px;
    padding: 10px;
    height: 50px;
    line-height: 50px;
    width: calc(100% - 10px);
    border: none;
    border: 1px solid var(--main-text-color-1);
    outline-style: none;
    box-sizing: border-box;
    font-family: OPPOSans-R;
    font-size: 15px;
}

.sys-input:focus {
    border: none;
    border: 1px solid var(--primary-color);
    outline: 0;
}

.sys-input-text-pre {
    text-align: right;
    margin: 5px;
    cursor: pointer;
}

.sys-input-text {
    margin: 5px;
    height: 300px;
    width: calc(100% - 10px);
    border: 1px solid var(--main-bg-color-1);
    outline-style: none;
    box-sizing: border-box;
    font-family: OPPOSans-R;
    font-size: 15px;
    resize: none;
}

.sys-input-pre {
    margin: 5px;
    height: 300px;
    width: calc(100% - 20px);
    font-family: OPPOSans-R;
    padding: 5px;
    overflow: auto;
    box-shadow: var(--box-shadow);
}

.sys-input-text:focus {
    border: none;
    border: 1px solid var(--primary-color);
    outline: 0;
}

.sys-select {
    width: 100%;
}

.sys-select-option-list {
    width: calc(100% - 20px);
    display: -webkit-flex; /* Safari */
    display: flex;
}

.sys-select-option {
    border-radius: 4px;
    margin: 5px;
    padding: 5px;
    background: var(--main-bg-color-0);
    cursor: pointer;
    box-shadow: var(--box-shadow);
}

.sys-select-option-active {
    border-radius: 4px;
    margin: 5px;
    padding: 5px;
    color: var(--primary-color);
    background: var(--main-bg-color-0);
    box-shadow: var(--box-shadow-active);
}


/*button*/
.sys-btn {
    height: 50px;
    width: calc(100% - 10px);
    line-height: 50px;
    margin: 5px;
    border-radius: 4px;
    background: var(--main-bg-color-0);
    color: var(--primary-color);
    font-weight: bold;
    text-align: center;
    cursor: pointer;
    box-shadow: var(--box-shadow);
}

.sys-btn-danger {
    height: 50px;
    width: calc(100% - 10px);
    line-height: 50px;
    margin: 5px;
    border-radius: 4px;
    background: red;
    color: white;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}

.sys-btn-blank {
    height: 50px;
    width: calc(100% - 10px);
    line-height: 50px;
    margin: 5px;
    border-radius: 4px;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}

.sys-menu-btn {
    height: 100px;
    width: calc(50% - 10px);
    margin: 5px;
    border-radius: 4px;
    background: var(--main-bg-color-0);
    color: var(--primary-color);
    text-align: center;
    cursor: pointer;
    float: left;
    /*font-size: 50px;*/
    display: -webkit-flex; /* Safari */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: var(--box-shadow);
}

/* chrome */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

/* 火狐浏览器 */
input[type="number"] {
    -moz-appearance: textfield;
}

.sys-text {
    font-family: var(--main-text-family);
}

/*msg*/
.okIcon::before {
    content: '';
    border: 3px solid green;
    border-left-width: 0;
    border-bottom-width: 0;
    transform: scaleX(-1) rotate(135deg);
    transform-origin: left top;
    display: block;
    animation: animate_check 0.5s linear 1 both;
}

@keyframes animate_check {
    0% {
        width: 0px;
        height: 0px;
    }
    33% {
        width: 20px;
        height: 0px;
    }
    100% {
        width: 20px;
        height: 40px;
    }
}

.sys-toast-mask {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0);
    display: -webkit-flex; /* Safari */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    /*backdrop-filter: blur(5px);*/
}

.smart-msg-div {
    position: fixed;
    top: 5px;
    height: 15px;
    line-height: 15px;
    width: 100vw;
    background: var(--main-bg-color-0);
    color: var(--main-text-color-0);
    z-index: 2;
    cursor: pointer;
    display: -webkit-flex; /* Safari */
    display: flex;
    justify-content: center;
}

.smart-msg-div-item {
    width: 34%;
}

.smart-msg-mid {
    height: 15px;
    width: 15px;
    background: var(--main-bg-color-1);
    box-shadow: 0 2px 4px 0 var(--main-text-color-1);
    border-radius: 50px;
    display: inline-block;
}

.sys-toast-msg {
    height: 150px;
    width: 150px;
    border-radius: 50px;
    border-radius: 10px;
    background: var(--main-bg-color-0);
    font-weight: bold;
    display: -webkit-flex; /* Safari */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: var(--box-shadow);
}


.sys-toast-icon {
    height: 100px;
    line-height: 100px;
    font-size: 80px;
}

.sys-toast-text {
    height: 50px;
    line-height: 25px;
    font-size: 15px;
    padding: 5px;
}

.sys-empty {
    margin: 5px;
    width: calc(100% - 10px);
    height: 30vh;
    line-height: 30vh;
    background: rgba(0, 0, 0, 0);
    text-align: center;
}

/*modal*/
.sys-popup-mask {
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5);
    box-shadow: 0 2px 4px 0 var(--main-text-color-0);
    backdrop-filter: blur(1px);
}

.sys-popup-head {
    padding-right: 10px;
    width: calc(100vw - 10px);
    height: 20px;
    text-align: right;
    font-size: 20px;
}

.sys-popup {
    position: fixed;
    bottom: 0px;
    left: 0;
    width: 100vw;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background: var(--main-bg-color-0);
    box-shadow: 0 2px 4px 0 var(--main-text-color-0);
}

.sys-popup-head {
    width: 100vw;
    height: 30px;
    line-height: 30px;
    text-align: center;
}

.sys-popup-content {
    max-height: 60vh;
    overflow-y: auto;
    min-height: 30vh;
    padding: 15px 10px 15px 10px;
    box-shadow: var(--box-shadow-active);
}

.sys-confirm-popup-content {
    padding: 15px 10px 15px 10px;
    box-shadow: var(--box-shadow-active);
}

.sys-confirm-head {
    width: 100vw;
    height: 30px;
    line-height: 30px;
    text-align: center;
}

/*list*/
.sys-list-item {
    display: -webkit-flex; /* Safari */
    display: flex;
    cursor: pointer;
}

.sys-list-item-icon {
    margin: 5px;
    padding: 5px;
    width: 50px;
    height: 50px;
    line-height: 50px;
    font-size: 30px;
    text-align: center;
    background: var(--main-bg-color-0);
    border-radius: 50px;
    box-shadow: var(--box-shadow);
}


.sys-list-item-icon.active {
    box-shadow: var(--box-shadow-active) !important;
}

.sys-list-item-content {
    margin: 5px;
    padding: 5px;
    width: calc(100vw - 10px - 60px - 45px);
    height: 50px;
    background: var(--main-bg-color-0);
    line-height: 50px;
    font-size: 20px;
    box-shadow: var(--box-shadow);
}

.sys-list-item-operate {
    margin: 5px;
    width: 100px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    display: -webkit-flex; /* Safari */
    display: flex;
}

.sys-tab {
    position: fixed;
    top: 70px;
    left: 5px;
    height: auto;
    width: 50px;
    background: var(--main-bg-color-0);
}

.sys-tab-item {
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: var(--main-text-color-0);
    cursor: pointer;
    box-shadow: var(--box-shadow);
}

.sys-tab-item-active {
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: var(--primary-color);
    font-weight: bold;
    box-shadow: var(--box-shadow-active);
}

.sys-tab-content {
    position: absolute;
    top: 0px;
    left: 60px;
    right: 10px;
    height: calc(100vh - 70px);
    width: calc(100% - 70px);
    color: var(--main-text-color-0);
}

/*list_menu */
.adm-swipe-action {
    --background: var(--main-bg-color-0) !important;
}

/*card*/
.sys-card {
    width: calc(100% - 10px);
    margin: 5px;
    background: var(--main-bg-color-0);
    border-radius: 4px;
    cursor: pointer;
    box-shadow: var(--box-shadow);
}

.sys-card-title {
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-weight: bold;
    border-bottom: 1px solid var(--main-text-color-1);
}

.sys-card-content {
    height: 100px;
    padding: 5px;
}

.sys-card-operate {
    height: 50px;
    width: 100%;
    display: -webkit-flex; /* Safari */
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid var(--main-text-color-1);
}

.sys-card-operate-item {
    height: 50px;
    line-height: 50px;
    width: 100%;
    text-align: center;
}

/*chart*/
.sys-process {
    width: 100%;
    height: 100%;
    border: 1px solid var(--main-bg-color-0);
}

.sys-process-percent {
    height: 100%;
    background: var(--primary-color);
    color: white;
}

/*通用*/
.primary-text {
    color: var(--primary-color);
}

.sys-tag-block {
    width: calc(100% - 10px);
    height: 30px;
    line-height: 30px;
    margin: 5px;
    font-size: 25px;
    font-weight: bold;
    text-align: center;
}

.sys-tag {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0);
    display: -webkit-flex; /* Safari */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    backdrop-filter: blur(5px);
}

.sys-tag-item {
    height: 150px;
    width: 150px;
    border-radius: 50px;
    border-radius: 10px;
    background: var(--main-bg-color-1);
    font-weight: bold;
    color: var(--main-text-color-0);
    display: -webkit-flex; /* Safari */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--main-text-color-1);
}

/* 滚动条 */
::-webkit-scrollbar { /*滚动条整体样式*/
    width: 5px; /*高宽分别对应横竖滚动条的尺寸*/
    height: 5px;
}

::-webkit-scrollbar-thumb { /*滚动条里面小方块*/
    border-radius: 4px;
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: var(--primary-color);
}

::-webkit-scrollbar-track { /*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    background: #EDEDED;
}

.change-animate {
    transition: 0.5s;
    -moz-transition: 0.5s; /* Firefox 4 */
    -webkit-transition: 0.5s; /* Safari and Chrome */
    -o-transition: 0.5s; /* Opera */
}

/*动画*/
.animate-circle {
    animation: rotate 1s infinite linear;
    /* Firefox: */
    -moz-animation: rotate 1s infinite linear;
    /* Safari and Chrome: */
    -webkit-animation: rotate 1s infinite linear;
    /* Opera: */
    -o-animation: rotate 1s infinite linear;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg)
    }
    100% {
        transform: rotate(360deg)
    }
}

@-moz-keyframes rotate /* Firefox */
{
    0% {
        transform: rotate(0deg)
    }
    100% {
        transform: rotate(360deg)
    }
}

@-webkit-keyframes rotate /* Safari and Chrome */
{
    100% {
        transform: rotate(360deg)
    }
}

@-o-keyframes rotate /* Opera */
{
    100% {
        transform: rotate(360deg)
    }
}

